.products{
    text-align: center;
    background-color: #ffffff;
    padding-top: 60px;
    padding-bottom: 60px;
    }
.products-text{
    font-family: 'Titan One';
    font-size: 18px;
    line-height: 1.16;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    
    color: #FFA5BA;
    margin-bottom: 15px;
@media screen and(min-width:768px){
        font-size: 26px;
        line-height: 1.15;
        margin-bottom: 18px;
    }
@media screen and(min-width:1280px){
    font-size: 30px;
    line-height: 1.13;
    margin-bottom: 16px;
    }
}
.products-title{
    font-family: 'Titan One';
    font-size: 34px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #D41443;
        margin-bottom: 198px;
@media screen and(min-width:768px){
            font-size: 48px;
            line-height: 1.15;  
            margin-bottom: 154px;
}
@media screen and(min-width:1280px){
            font-size: 58px;
            line-height: 1.14;
            margin-bottom: 262px;
}
        
}  
.products-list{
    list-style: none;
    padding-inline-start: 0px;
@media screen and (min-width:768px){
    display: flex; 
    justify-content: center;
    }
}
.products-list__item{
@media screen and (min-width:768px){
        &:not(:last-child){
            margin-right: 16px;
            }
}  
@media screen and (min-width:1280px){
        &:not(:last-child){
            margin-right: 32px;
            }
}  
position: relative;
}
.products-list__title{
    font-family: 'Titan One';
    font-size: 20px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #FFFFFF;
    margin-bottom: 32px;
    padding-top: 20px;
@media screen and (min-width:1280px){
        font-size: 30px;
        line-height: 1.2; 
        padding-top: 52px; 
            }
&::after{
    content:url("../images/icon/group4.png");
    display: block;
    text-align: center;
    margin-top: 32px;
}
} 
.products-list__text{
    font-family: 'DM Sans';
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    letter-spacing: 0.04em;
    color: #FFFFFF;
    margin-bottom: 32px;
@media screen and (min-width:768px){
    font-size: 14px;
    line-height: 1.76; 
}
@media screen and (min-width:1280px){
    font-size: 16px;
line-height: 1.76; 
}
}
.products-list__btn{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: none;
    
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus{
    background-color:#C7133E ;
    color: #FFFFFF;
    }
&:hover .icon-arrow-right,
&:focus .icon-arrow-right{
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    fill: #ffffff;  
}
}
.cards-ice-cream{
    background-color: #FFB8CA;
    
&::before{
    content: url("../images/products/home-pic1-mob-1x.png");
    width: 100%;
    height: 280px;
    display: inline-block;
    position: absolute;
    left: 0;
    top:  -40%;
@media screen and (min-width:480px){
    content: url("../images/products/home-pic1-mob-1x.png");  
    left:0;
    top:-45%; 
}
@media screen and (min-width:768px){
    content: url("../images/products/home-pic1-tab-1x.png");  
    left:0;
    top:-30%; 
}
@media screen and (min-width:1280px){
        content: url("../images/products/home-pic1-1x.png");  
        left:0;
        top:-50%; 
            }
}      
}
