    .cards{
        min-width: 224px;
        margin-top: 198px;
        padding-top: 100px;
        padding-bottom: 40px;
        position: relative;
        border-radius: 24px;
        padding-left: 40px;
        padding-right: 40px;
@media screen and(min-width:768px){
        margin-top: 0;
        padding-left: 32px;
        padding-right: 32px;  
        }
@media screen and(min-width:1280px){
        padding-left: 76px;
        padding-right: 76px;  
        padding-bottom: 60px;
        }
}
.icon-arrow-right{
        fill: #C7133E;
    &:hover,
    &:focus{
        fill: currentColor;
        }
}
    .popup{
        position: fixed;
        width: 100%;
        height: 100%;
        background-color: rgba(36, 25, 33, 0.8);
        top: 0;
        left: 0;
        z-index: 100;
        overflow-y: auto;
        overflow-x: hidden;
        transition: all 0.8s ease 0s;
    }
        .popup-body {
        min-height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 30px 10px;
        text-align: center;
        }
    .popup-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        background-image: url(../images/img-how-made/milk-bg-desk-1x.png);
        color: #D41443;
        max-width: 800px;
        padding: 30px;
        position: relative;
        transition: all 0.8s ease 0s;
        box-shadow: 0px 5px 10px rgba(229, 217, 217, 0.12), 0px 5px 10px rgba(99, 65, 65, 0.078), 0px 5px 10px rgba(188, 143, 143, 0.2);
        }
    .popup-bt {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        border: none;
        transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus{
        background-color:#C7133E ;
        color: #FFFFFF;
        }
    &:hover .icon-arrow-right,
    &:focus .icon-arrow-right{
        transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
        fill: #ffffff;  
    }
}
    .popup-title {
        font-family: 'Titan One';
        font-size: 34px;
        line-height: 1.15;
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #D41443;
        margin-bottom: 40px;
        }
.popup-text {
        font-family: 'DM Sans';
        font-weight: 700;
        font-size: 14px;
        line-height: 1.52;
        letter-spacing: 0.04em;
        color: #FFFFFF;
        margin-bottom: 30px;
@media screen and (min-width:768px){
    font-size: 14px;
    line-height: 1.76; 
}
@media screen and (min-width:1280px){
    font-size: 16px;
line-height: 1.76;
}

}
.is-hidden{
        opacity: 0;
        pointer-events: none;
        visibility: hidden;
        }